body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
	--primary: #0d253f;
	--secondary: #01b4e4;
	--tertiary: #90cea1;
	--text-color: #f5c518;
  }

@font-face {
	font-family: "ACFilmstrip";
	src: url("./assets/fonts/ACFilmstrip.otf")
}
@font-face {
	font-family: "moviefilmstrip";
	src: url("./assets/fonts/movie-filmstrip.regular.ttf")
}
@font-face {
	font-family: "undeveloped";
	src: url("./assets/fonts/undeveloped.book-regular.ttf")
}
@font-face {
	font-family: "bellefair";
	src: url("./assets/fonts/Bellefair-Regular.ttf")
}


.font-face-acfilmstrip {
	font-family: "ACFilmstrip";
	font-size-adjust: 0.58;
}

.font-face-mfstrip {
	font-family: "moviefilmstrip";
	font-size-adjust: 0.58;
}

.font-face-undeveloped {
	font-family: "undeveloped";
	font-size-adjust: 0.58;
}

.font-face-bellefair {
	font-family: "bellefair";
	font-size-adjust: 0.58;
}

/*  CSS reset  */


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
